<script>

    export let data;
    export let filters;

    let lot_th = filters[0];
    let far_th = filters[1];

    let stabs = 0
    let no_stabs = 0


    data = data.filter( function(d){
        if(filters[2] === "All NYC"){
            return d;
        }else{
            return d.puma === filters[2]
        }
    })


    data.forEach(element => {
        if( element['st_2007'] > 0){
            stabs = stabs + 1
        }else{
            no_stabs = no_stabs + 1
        }    
    });


    let perc_stab = Math.round(stabs * 100 / data.length)

    let filtered_data = data.filter( function(d){
        return (d['LotArea_07'] > lot_th ) && (d['availFAR_perc_07'] > far_th ) ;
    })

</script>

<div class="reports">

    <span class="report-title">
    {#if filters[2] != "All NYC"}
        PUMA: {filters[2]}
    {:else}
        All NYC
    {/if}
    </span>

    <p id="1"># of Developped Lots: {data.length}</p>
    <p id="2">Covered Lot %: {Math.round(filtered_data.length*100/data.length)}%</p>
    <p id="3">% of Lots with Rent St. Units: {perc_stab}%</p>


</div>

<style>

    .report-title{
        font-weight: bold;
    }

    .reports{
        display: flex;
        flex-direction: column;
    }

    .reports > p {
        margin:0;
        font-size: 12pt;
    }

</style>